.cursor {
    position: absolute;
    width: 300px;
    height: 300px;
    background: radial-gradient(rgba(white, 1) 0%, transparent 80%);
    border-radius: 400px;
    filter: blur(18px);
    mix-blend-mode: overlay;
    z-index: 2;
    overscroll-behavior: contain;
    pointer-events: none;
  }